@import '~antd/dist/antd.css';

h1#pTitle {
    font-family: Raleway, sans-serif;
    font-size: 72px;
    letter-spacing: 5px;
    line-height: 30px;
}

#GTextBox {
    width: 50vw;
    max-width: 550px;
    text-align: center;
}

#shape1 {
    position: absolute;
    left: 10vw;
    top: 48vh;
    border: 2px solid #f0f5ff;
    border-radius: 20vw;
    width: 20vw;
    height: 20vw;
    background-color: #fafcfebe;
    box-shadow: 10px 10px 50px -5px #d9d9d9;
}

#shape2 {
    position: absolute;
    right: 8vw;
    top: -10vh;
    border: 2px solid #f0f5ff;
    transform: rotate(45deg);
    width: 20vw;
    height: 20vw;
    background-color: #fafcfebe;
    box-shadow: 10px 10px 50px -5px #d9d9d9;
}